<template>
    <div class="feedback">
        <yd-layout>
            <yd-navbar slot="navbar" title="填写反馈">
                <router-link to="/home/monitor" slot="left">
                    <yd-navbar-back-icon></yd-navbar-back-icon>
                </router-link>
                 <router-link to="/search" slot="right">
                    <yd-icon name="search" size="25px" color="#777"></yd-icon>
                </router-link> 
            </yd-navbar>
            <yd-cell-group class="main">
                <yd-cell-item>
                    <span slot="left">标题：</span>
                    <yd-input slot="right" v-model="message.title" ref="title" max="20" required :show-required-icon="false" placeholder="请输入反馈标题"></yd-input>
                </yd-cell-item>
            </yd-cell-group>
            <yd-cell-group title="内容">
                <yd-cell-item>
                    <yd-textarea slot="right" v-model="message.content" ref="content" placeholder="请输入返回内容" required maxlength="200"></yd-textarea>
                </yd-cell-item>
            </yd-cell-group>
            <yd-button-group>
                <yd-button size="large" @click.native="submit">提交</yd-button>
            </yd-button-group>
        </yd-layout>
    </div>
</template>
<script>
import { Inputs } from 'vue-ydui/dist/lib.rem/input';

export default {
    name:'feedback',
    data() {
        return {
            message: {
                action:"feedback",
                title: '',
                content: ''
            }
        }
    },
    methods: {
        submit() {
            var _this = this;
            var message = '';
            if (this.$refs.title.iserror) {
                message = '标题'+this.$refs.title.errorMsg;
            }else if(this.message.content == ''){
                message = '请输入内容';
            }else if(this.$refs.content.iserror){
                message = '内容'+this.$refs.content.errorMsg;
            }
            //提示信息,阻止运行
            if(message !== ''){
                this.$dialog.toast({
                    mes: message,
                    timeout: 1500,
                    icon: 'error'
                });
                return;
            }
            this.$dialog.loading.open('上传数据');
            //'/static/mock/feedback.json'
            //'/weixin/wxhandler.ashx'
            this.$http.post(this.api.feedback,this.message,{
                headers:{'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'},
                emulateJSON: true
            })
            .then((response)=>{
                var res= response.body;
                if(res.error === 0){
                    this.$dialog.toast({
                        mes:res.data,
                        timeout:2500,
                        icon:'success',
                        callback:()=>{
                            // this.$router.push('/serach');
                        }
                    });
                }else{
                    this.$dialog.toast({
                        mes:res.data,
                        timeout:2500,
                        icon:'error'
                    });
                }
                this.$dialog.loading.close();
            },(response)=>{
                this.$dialog.alert({
                    mes:'Error:'+response.status+' '+response.statusText
                });
                this.$dialog.loading.close();
            });
            
        }
    },
    components: {
        Inputs
    }
}
</script>
<style scoped>
.feedback {
    flex-grow: 1;
}

.feedback .main {
    margin-top: 10px;
}
</style>





