<template>
    <div class="serach">
        <yd-navbar slot="navbar" title="反馈记录">
            <router-link to="/home/feedback" slot="left">
                <yd-navbar-back-icon>用户反馈</yd-navbar-back-icon>
            </router-link>
            <yd-icon slot="right" @click.native="refresh" name="refresh" size="25px" color="#777"></yd-icon>
        </yd-navbar>
        <div class="list-box">
            <div class="list-content">
                <yd-infinitescroll :callback="pullRefresh" ref="infinitescroll">
                    <div slot="list">
                        <div class="list-item" v-for="(val,index) in resultList" :key="val.Id">
                            <div class="title">{{val.title}}</div>
                            <div class="content">{{val.content}}</div>
                        </div>
                    </div>
                </yd-infinitescroll>

            </div>
        </div>
    </div>
</template>
<script>
import { NavBar, NavBarBackIcon, NavBarNextIcon } from 'vue-ydui/dist/lib.rem/navbar';
import { InfiniteScroll } from 'vue-ydui/dist/lib.rem/infinitescroll';
import { Icons } from 'vue-ydui/dist/lib.rem/icons';
export default {
    name:'search',
    data() {
        return {
            overlayShow: true,
            timer: null,
            value: '',
            resultList: [],
            message: {
                action: 'feedbacklist',
                pagesize: 8,
                pageidx: 1
            }
        }
    },
    created() {
        this.initLoad();
    },
    methods: {
        //开始查询
        pullRefresh() {

            //'/static/mock/search.json'
            //'/weixin/wxhandler.ashx'
            this.message.pageidx++;
            this.$http.post(this.api.search, this.message, {
                headers: { 'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8' },
                emulateJSON: true
            })
                .then((response) => {
                    var res = response.body;
                    if (res.error === 0) {
                        var dataLen = res.data.length;
                        this.resultList = this.resultList.concat(res.data);

                        if (dataLen === 0 || dataLen < this.message.pagesize) {
                            this.$refs.infinitescroll.$emit('ydui.infinitescroll.loadedDone');
                        } else {
                            this.$refs.infinitescroll.$emit('ydui.infinitescroll.finishLoad');
                        }

                    } else {
                        this.$dialog.toast({
                            mes: '搜索失败',
                            timeout: 2500,
                            icon: 'error'
                        });
                    }
                }, (response) => {
                    this.$dialog.alert({
                        mes: 'Error:' + response.status + ' ' + response.statusText
                    });
                });
        },
        initLoad() {
            this.$dialog.loading.open();
            //'/static/mock/search.json'
            //'/weixin/wxhandler.ashx'
            this.$http.post(this.api.search, this.message, {
                headers: { 'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8' },
                emulateJSON: true
            })
                .then((response) => {
                    var res = response.body;
                    if (res.error === 0) {
                        var dataLen = res.data.length;
                        this.resultList = res.data;
                        if(dataLen.length === 0 || dataLen<this.message.pagesize){
                            this.$refs.infinitescroll.$emit('ydui.infinitescroll.loadedDone');
                        }else{
                            this.$refs.infinitescroll.$emit('ydui.infinitescroll.finishLoad');
                        }
                    } else {
                        this.$dialog.toast({
                            mes: '搜索失败',
                            timeout: 2500,
                            icon: 'error'
                        });
                    }
                    this.$dialog.loading.close();
                }, (response) => {
                    this.$dialog.alert({
                        mes: 'Error:' + response.status + ' ' + response.statusText
                    });
                    this.$dialog.loading.close();
                });
        },
        //刷新
        refresh() {
            this.message.pageidx = 1;
            this.resultList = [];
            this.$refs.infinitescroll.$emit('ydui.infinitescroll.reInit');
            this.initLoad();
        }
    },
    components: {
        NavBar,
        NavBarBackIcon,
        NavBarNextIcon,
        InfiniteScroll,
        Icons
    }
}
</script>
<style scoped>
.serach {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: #f5f5f5;
}

.list-box {
    position: relative;
    flex-grow: 1;
}

.list-content {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow-y: auto;
}

.list-item {
    padding: 14px;
    text-align: left;
    border-top: 1px solid #E5E5E5;
    background-color: #fff;
}

.title {
    font-weight: 400;
    font-size: 16px;
}

.content {
    font-size: 14px;
}
</style>




