
//本地存储设置
var sessionStorage = {
    getSession:function(name){
        return window.JSON.parse(window.sessionStorage.getItem(name));
    },
    setSession:function(name,value){
        window.sessionStorage.setItem(name,window.JSON.stringify(value));
    },
    removeSession:function(name){
        window.sessionStorage.removeItem(name);
    }
};
//获取url query参数
var getQueryParams = function(name){
    //match [2]是第二个子表达式匹配上的值
    var reg = new RegExp('(^|&)'+name+'=([^&]*)(&|$)');
    var matchStr = window.location.search.slice(1).match(reg);
    return matchStr === null?matchStr:decodeURI(matchStr[2]);
}

module.exports = {
    sessionStorage,
    getQueryParams
};


