<template>
  <div class="home">
    <div class="main">
      <!-- <transition :name="transitionName"> -->
          <router-view></router-view>
      <!-- </transition> -->
    </div>
    <div class="navigation">
      <div class="nav-item" @click="toggle('monitor')">
        <div class="nav-icon">
          <yd-icon name="home-outline" size="0.54rem"></yd-icon>
        </div>
        <div class="nav-name">设备监控</div>
      </div>
      <div class="nav-item" @click="toggle('feedback')">
        <div class="nav-icon">
          <yd-icon name="feedback" size="0.54rem"></yd-icon>
        </div>
        <div class="nav-name">用户反馈</div>
      </div>
      <div class="nav-item" @click="toggle('user')">
        <div class="nav-icon">
          <yd-icon name="ucenter-outline" size="0.54rem"></yd-icon>
        </div>
        <div class="nav-name">用户信息</div>
      </div>
    </div>
  </div>
</template>
<script>
import { TabBar, TabBarItem } from "vue-ydui/dist/lib.rem/tabbar";
export default {
  name: "home",
  data() {
    return {
      index: 0
    };
  },
  methods:{
    toggle(path){
      this.$router.replace(path);
    }
  },
  components: {
    TabBarItem,
    TabBar
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.home {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #f5f5f5;
}
.main {
  flex-grow: 1;
  display: flex;
}
.main .active {
  color: #000;
}
.navigation {
  display: flex;
  flex-direction: row;
  background-color: #fff;
  border-top: 1px solid #ccc; 
}
.nav-item {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 0.24rem;
  color: rgb(151, 151, 151);
}
.nav-icon{
  /* height: .5832rem; */
}
</style>





