<template>
  <div class="loding">
    <img src="../../assets/loading/loading.gif">
  </div>
</template>
<script>
export default {
  name:'welcome',
  data() {
    return {
      message:{
        weichatid:''
      }
    }
  },
  created() {
    var _this = this;
    this.message.weichatid = this.util.getQueryParams('openid');
    
    this.util.sessionStorage.removeSession('userinfo');
    this.$http
      .post(this.api.EasyLoginWx, this.message, {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"
        },
        emulateJSON: true
      })
      .then(
        response => {
          var res = response.body;
          if (res.error === 0) {
            //存储用户信息
            this.util.sessionStorage.setSession('userinfo',res.data);
            this.$router.replace({ name: "monitor" });
          } else if(res.error === 1){
            this.$router.replace("/login");
          }else {
            this.$dialog.toast({
              mes: res.data,
              timeout: 2500,
              icon: "error"
            });
          }
        },
        response => {
          this.$dialog.alert({
            mes: "Error:" + response.status + " " + response.statusText
          });
        }
      );

  }
};
</script>
<style scoped>
.loding {
  width: 100%;
  height: 100%;
  background-color: #fff;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.loding img {
  width: 200px;
  height: 200px;
}
</style>




