<template>
  <div class="not-found-page" @click="jumpHome()">
      <div class="box">
          <div class="title">404</div>
          <div class="content">找不到页面</div>
      </div>
  </div>
</template>

<script>
export default {
  name: 'notFoundPage',
  data(){
    return {}
  },
  methods:{
      jumpHome(){
          this.$router.replace({name:'monitor'});
      }
  }
}
</script>

<style scoped>
.not-found-page{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.box{
    width: 100%;
}
.title{
    height: 50px;
    line-height: 50px;
    font-size: 40px;
}
.content{
    font-size: 20px;
}
</style>



