<template>
    <div class="chart">
        <yd-navbar slot="navbar" :title="title">
            <div @click="back()" slot="left">
                <yd-navbar-back-icon></yd-navbar-back-icon>
            </div>
        </yd-navbar>
        <div class="chart-box" ref="charts"></div>
    </div>
</template>
<script>
import echarts from "echarts/lib/echarts";
import "echarts/lib/chart/line";

export default {
  name: "chart",
  data() {
    return {
      title: "",
      typeName: [
        "A相电流",
        "B相电流",
        "C相电流",
        "A相电压",
        "B相电压",
        "C相电压",
        "A相电压1",
        "B相电压1",
        "C相电压1",
        "漏电流",
        "1路温度",
        "2路温度",
        "3路温度",
        "4路温度",
        "补位"
      ],
      message: {
        action: "chat",
        type: "",
        itemid: ""
      },
      chart: null,
      chartData:[],
      chartDate:[]
    };
  },
  mounted() {
    var _this = this;
    this.message.type = this.$route.params.type;
    this.message.itemid = this.$route.params.id;
    
    this.title = this.typeName[this.message.type];
    //初始化图表
    this.chart = echarts.init(this.$refs.charts);
    this.chart.setOption({
        tooltip: {},
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: []
        },
        yAxis: {
          type: "value",
          boundaryGap: [0, "50%"]
        },
        series: [
          {
            name: this.title,
            type: "line",
            smooth:true,//是否光滑显示
            data: []
          }
        ]
      });
    // '/weixin/wxhandler.ashx'
    //"/static/mock/chartData.json"
    this.chart.showLoading();
    this.$http.post(this.api.chartData, this.message, {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"
        },
        emulateJSON: true
      })
      .then(
        response => {
          this.chart.hideLoading();
          var res = response.body;
          if (res.error === 0) {
              //设置图表数据
              this.setChartData(res.data);
              this.chart.setOption({
                  xAxis:{
                      data:this.chartDate
                  },
                  series:[{
                      name: this.title,
                      data: this.chartData
                  }]
              });
          } else {
            this.$dialog.toast({
              mes: "获取失败",
              timeout: 2500,
              icon: "error"
            });
          }
          this.$dialog.loading.close();
        },
        response => {
          this.chart.hideLoading();
          this.$dialog.alert({
            mes: "Error:" + response.status + " " + response.statusText
          });
          this.$dialog.loading.close();
        }
      );
  },
  methods: {
    back() {
      this.$router.back();
    },
    setChartData(data){
        for(var i = 0;i<data.length;i++){
            this.chartData.push(data[i].value);
            this.chartDate.push(data[i].Minute+'H');
            // this.chartDate.push(eval('+new '+data[i].updatetime.replace(/\//g,'')));
        }
        
    }
  }
};
</script>
<style scoped>
.chart {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}
.chart-box {
  flex-grow: 1;
  position: relative;
}
</style>





