<template>
  <div class="login">
    <yd-navbar slot="navbar" title="用户登录"></yd-navbar>
    <div class="main">
      <yd-cell-group>
          <yd-cell-item>
              <span slot="left">用户名：</span>
              <input slot="right" type="text" v-model="message.username" placeholder="请输入用户名">
          </yd-cell-item>
          <yd-cell-item>
              <span slot="left">密&nbsp;&nbsp;&nbsp;&nbsp;码：</span>
              <input slot="right" type="text" v-model="message.password" placeholder="请输入密码">
          </yd-cell-item>
          <div class="checkcode-box">
        <div class="checkcode-input">
          <yd-cell-item>
              <span slot="left">验证码：</span>
              <input slot="right" type="text" v-model="message.vcode" placeholder="请输入验证码">
          </yd-cell-item> 
        </div>
        <div class="checkcode-img">
          <img :src="checkCode" @click="refreshCheckCode()" alt="点击刷新验证码">
        </div>
      </div>
      </yd-cell-group>
      <yd-button-group>
        <yd-button size="large" type="primary" @click.native="login">登&nbsp;&nbsp;&nbsp;&nbsp;录</yd-button>
      </yd-button-group>

    </div>
  </div>
</template>
<script>
export default {
  name: "login",
  data() {
    return {
      checkCode: "",
      message: {
        username: "",
        password: "",
        vcode: ""
      }
    };
  },
  created() {
    this.refreshCheckCode();
  },
  methods: {
    login() {
      var message = "";
      if (!this.message.username) {
        message = "请输入用户名";
      } else if (!this.message.password) {
        message = "请输入密码";
      } else if (!this.message.vcode) {
        message = "请输入验证码";
      }

      if (message !== "") {
        this.$dialog.toast({
          mes: message,
          timeout: 1500,
          icon: "error"
        });
        return;
      }

      this.$dialog.loading.open("正在登录中...");
      this.$http
        .post(this.api.login, this.message, {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"
          },
          emulateJSON: true
        })
        .then(
          response => {
            var res = response.body;
            if (res.error === 0) {
              //存储用户信息
              this.util.sessionStorage.setSession("userinfo", res.data);
              this.$dialog.toast({
                mes: "用户登陆成功",
                timeout: 1500,
                icon: "success",
                callback: () => {
                  this.$router.replace({ name: "monitor" });
                }
              });
            } else {
              this.$dialog.toast({
                mes: res.data,
                timeout: 2500,
                icon: "error"
              });
              this.refreshCheckCode();
            }
            this.$dialog.loading.close();
          },
          response => {
            this.$dialog.alert({
              mes: "Error:" + response.status + " " + response.statusText
            });
            this.$dialog.loading.close();
          }
        );
    },
    refreshCheckCode() {
      this.checkCode = this.api.checkCode + "?random=" + Math.random();
    }
  },
  components: {}
};
</script>
<style scoped>
.login {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #fff;
}
.main {
  width: 100%;
  flex-grow: 1;
}
.checkcode-box {
  display: flex;
  flex-direction: row;
}
.checkcode-input {
  flex-grow: 1;
}
.checkcode-img {
  flex-grow: 2;
  display: flex;
  align-items: center;
}
</style>




