import Vue from 'vue'
import Router from 'vue-router'
import welcome from '@/components/welcome/welcome'
import login from '@/components/login/login'
import home from '@/components/home/home'
import monitor from '@/components/monitor/monitor'
import list from '@/components/list/list'
import feedback from '@/components/feedback/feedback'
import user from '@/components/user/user'
import search from '@/components/search/search'
import chart from '@/components/chart/chart'
import notFoundPage from '@/components/notFoundPage/notFoundPage'
import util from '../util'


Vue.use(Router)

const router = new Router({
  linkActiveClass: 'action',
  routes: [{
    path: '/',
    name: 'welcome',
    component: welcome
  }, {
    path: '/login',
    name: 'login',
    component: login
  }, {
    path: '/home',
    name: 'home',
    component: home,
    children: [{
      name: 'monitor',
      path: 'monitor',
      component: monitor,
      meta: {
        loginAuth: true
      }
    }, {
      name: 'list',
      path: 'list',
      component: list,
      meta: {
        loginAuth: true
      }
    }, {
      name: 'feedback',
      path: 'feedback',
      component: feedback,
      meta: {
        loginAuth: true
      }
    }, {
      name: 'user',
      path: 'user',
      component: user,
      meta: {
        loginAuth: true
      }
    }]
  }, {
    path: '/search',
    name: 'search',
    component: search,
    meta: {
      loginAuth: true
    }
  }, {
    path:"/chart/:type/:id",
    name: 'chart',
    component: chart,
    meta: {
      loginAuth: true
    },
    beforeEnter(to, from, next) {
      var {params} = to;
      if (params.type !== undefined || params.id !== undefined) {
        next();
      } else {
        next('/');
      }
      next();
    }
  }, {
    path: '*',//*为当所有路由都查找不到的时候调用
    name: 'notFountPage',
    component: notFoundPage,
    meta:{
      loginAuth:true
    }
  }]
});

//全局导航监控
router.beforeEach((to, from, next) => {
  console.log(to,from);
  var openid = util.getQueryParams('openid');
  console.log(openid);
  // if(!openid){
  //   next({name:'notFountPage'});
  //   return;
  // }
  var userinfo = util.sessionStorage.getSession('userinfo');
  //判断当前跳转路由是否需要登录校验
  if (to.matched.some(val => val.meta.loginAuth)) {
    if (!userinfo) {
      next('/')
    } else {
      next();
    }
  } else {
    next();
  }

});

export default router;



