<template>
  <div class="user">
    <div class="user-navigation">
        <yd-navbar slot="navbar" title="用户信息">
            <router-link to="/home/monitor" slot="left">
                <yd-navbar-back-icon></yd-navbar-back-icon>
            </router-link>
        </yd-navbar>
    </div>
    
    <div class="main">
        <div class="user-item">
            <div class="user-avatar">
                <yd-icon name="ucenter" size="1rem" color="#ccc"></yd-icon>
                <!-- <yd-icon v-if="avatarComplete" name="ucenter" size="1rem" color="#ccc"></yd-icon>
                <img src="/aaa/img.png" v-if="!avatarComplete" :onerror="imgError()"> -->
            </div>
            <div class="user-info">
                <div class="user-name">{{realName}}</div>
                <div class="user-phone">{{phone}}</div>
            </div>
        </div>

        <yd-cell-group class="list">
            <yd-cell-item type="link" href="/home/monitor" arrow>
                <yd-icon slot="icon" name="home-outline" size=".42rem"></yd-icon>
                <span slot="left">设备监控</span>
                <span slot="right"></span>
            </yd-cell-item>

            <yd-cell-item type="link" href="/home/feedback" arrow>
                <yd-icon slot="icon" name="feedback" size=".42rem"></yd-icon>
                <span slot="left">用户反馈</span>
                <span slot="right"></span>
            </yd-cell-item>
        </yd-cell-group>

        <yd-button-group>
            <yd-button v-if="isShowBindBut" size="large" type="primary" @click.native="bind">绑定用户</yd-button>
        </yd-button-group>
        <yd-button-group>
            <yd-button v-if="!isShowBindBut" size="large" type="danger" @click.native="unbind">解绑用户</yd-button>
        </yd-button-group>
    </div>
  </div>
</template>
<script>
import { Icons } from "vue-ydui/dist/lib.rem/icons";
import { CellGroup, CellItem } from "vue-ydui/dist/lib.rem/cell";
export default {
  name:'user',
  data() {
    return {
      avatarComplete: false,
      realName: "",
      phone: "",
      isShowBindBut:true,
      message: {
        action: "",
        token: ""
      }
    };
  },
  created() {
    var userinfo = this.util.sessionStorage.getSession("userinfo")||{};
    this.realName = userinfo.real_name;
    this.phone = userinfo.telephone;
    this.isShowBindBut = userinfo.IsBind==0?true:false;

    this.message.token = this.util.getQueryParams('openid');
  },
  methods: {
    imgError: function() {
      this.avatarComplete = true;
    },
    //绑定账号
    bind() {
      this.$dialog.confirm({
        title: "绑定账号",
        mes: "是否关联微信账号?",
        opts: () => {
          this.bindOrUnbind(this.api.bind,"bind", "正在绑定中",1);
        }
      });
    },
    //解绑账号
    unbind() {
      this.$dialog.confirm({
        title: "解绑账号",
        mes: "是否解除关联微信账号?",
        opts: () => {
          this.bindOrUnbind(this.api.unbind,"unbind", "正在解绑中",0);
        }
      });
    },
    bindOrUnbind(api,action,prompt,IsBind) {
      this.message.action = action;
      this.$dialog.loading.open(prompt);
      this.$http
        .post(api, this.message, {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"
          },
          emulateJSON: true
        })
        .then(
          response => {
            var res = response.body;
            if (res.error === 0) {
              //设置绑定状态
              var userinfo = this.sessionStorage.getSession('userinfo');
              userinfo.IsBind = IsBind;
              this.sessionStorage.setSession('userinfo',userinfo);
              this.isShowBindBut = IsBind==0?true:false;

              this.$dialog.toast({
                mes: "请求成功",
                timeout: 1500,
                icon: "success"
              });
            } else {
              this.$dialog.toast({
                mes: res.data,
                timeout: 2500,
                icon: "error"
              });
            }
            this.$dialog.loading.close();
          },
          response => {
            this.$dialog.alert({
              mes: "Error:" + response.status + " " + response.statusText
            });
            this.$dialog.loading.close();
          }
        );
    }
  },
  components: {
    Icons,
    CellGroup,
    CellItem
  }
};
</script>
<style scoped>
.user {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}
.user-navigation {
}
.main {
  flex-grow: 1;
}
.user-item {
  display: flex;
  flex-direction: row;
  padding: 15px;
  background-color: #fff;
  border-top: 1px solid #e5e5e5;
  border-bottom: 1px solid #e5e5e5;
  margin-top: 16px;
}
.user-avatar {
  width: 60px;
  height: 60px;
  line-height: 60px;
  margin-right: 0.8em;
  position: relative;
}
.user-avatar img {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}
.user-info {
  flex-grow: 1;
  text-align: left;
}
.user-name {
  font-size: 16px;
  font-weight: 400;
}
.list {
  margin-top: 10px;
}
.user-phone {
  margin-top: 8px;
}
</style>





