<template>
    <div class="monitor">
        <yd-navbar title="监控中心">
            <yd-icon slot="right" @click.native="refresh" name="refresh" size="25px" color="#777"></yd-icon>
        </yd-navbar>
        <div class="container">
            <!--侧边栏-->
            <yd-popup v-model="popShow" position="left" width="60%">
                <div class="switch-list">
                    <div class="switch-item">
                        <yd-switch v-model="voltage"></yd-switch>
                        <span>电压</span>
                    </div>
                    <div class="switch-item">
                        <yd-switch v-model="electricCurrent"></yd-switch>
                        <span>电流</span>
                    </div>
                    <div class="switch-item">
                        <yd-switch v-model="temperature"></yd-switch>
                        <span>温度</span>
                    </div>
                    <div class="switch-item">
                        <yd-switch v-model="electricLeakage"></yd-switch>
                        <span>漏电</span>
                    </div>
                </div>
            </yd-popup>
            <div class="list-box">
                <yd-infinitescroll :callback="pullRefresh" ref="infinitescroll">
                    <yd-accordion v-if="list[0]" :accordion="true" slot="list">
                        <yd-accordion-item v-for="(val,index) in list" :title="val.name" :key="val.id">
                            <!--A相电流-->
                            <div class="list-item" v-show="electricCurrent">
                                <div class="item-title">A相电流</div>
                                <div class="item-content">
                                    <span>{{val.AI}}</span>
                                    <span class="unit">A</span>
                                    <span class="items-href" @click="toChartPage(val.id,1)">
                                        <yd-icon name="share1" size="14px" color="#16AA6B"></yd-icon>
                                    </span>
                                </div>
                            </div>
                            <!--B相电流-->
                            <div class="list-item" v-show="electricCurrent">
                                <div class="item-title">B相电流</div>
                                <div class="item-content">
                                    <span>{{val.BI}}</span>
                                    <span class="unit">A</span>
                                    <span class="items-href" @click="toChartPage(val.id,2)">
                                        <yd-icon name="share1" size="14px" color="#16AA6B"></yd-icon>
                                    </span>
                                </div>

                            </div>
                            <!--C相电流-->
                            <div class="list-item" v-show="electricCurrent">
                                <div class="item-title">C相电流</div>
                                <div class="item-content">
                                    <span>{{val.CI}}</span>
                                    <span class="unit">A</span>
                                    <span class="items-href" @click="toChartPage(val.id,3)">
                                        <yd-icon name="share1" size="14px" color="#16AA6B"></yd-icon>
                                    </span>
                                </div>

                            </div>
                            <!--漏电电流-->
                            <div class="list-item" v-show="electricCurrent">
                                <div class="item-title">漏电电流</div>
                                <div class="item-content">
                                    <span>{{val.LI}}</span>
                                    <span class="unit">A</span>
                                    <span class="items-href" @click="toChartPage(val.id,10)">
                                        <yd-icon name="share1" size="14px" color="#16AA6B"></yd-icon>
                                    </span>
                                </div>

                            </div>
                            <!--1路温度-->
                            <div class="list-item" v-show="temperature">
                                <div class="item-title">1路温度</div>
                                <div class="item-content">
                                    <span>{{val.Temp1}}</span>
                                    <span class="unit">℃</span>
                                    <span class="items-href" @click="toChartPage(val.id,11)">
                                        <yd-icon name="share1" size="14px" color="#16AA6B"></yd-icon>
                                    </span>
                                </div>

                            </div>
                            <!--2路温度-->
                            <div class="list-item" v-show="temperature">
                                <div class="item-title">2路温度</div>
                                <div class="item-content">
                                    <span>{{val.Temp2}}</span>
                                    <span class="unit">℃</span>
                                    <span class="items-href" @click="toChartPage(val.id,12)">
                                        <yd-icon name="share1" size="14px" color="#16AA6B"></yd-icon>
                                    </span>
                                </div>

                            </div>
                            <!--3路温度-->
                            <div class="list-item" v-show="temperature">
                                <div class="item-title">3路温度</div>
                                <div class="item-content">
                                    <span>{{val.Temp3}}</span>
                                    <span class="unit">℃</span>
                                    <span class="items-href" @click="toChartPage(val.id,13)">
                                        <yd-icon name="share1" size="14px" color="#16AA6B"></yd-icon>
                                    </span>
                                </div>

                            </div>
                            <!--4路温度-->
                            <div class="list-item" v-show="temperature">
                                <div class="item-title">4路温度</div>
                                <div class="item-content">
                                    <span>{{val.Temp4}}</span>
                                    <span class="unit">℃</span>
                                    <span class="items-href" @click="toChartPage(val.id,14)">
                                        <yd-icon name="share1" size="14px" color="#16AA6B"></yd-icon>
                                    </span>
                                </div>

                            </div>
                        </yd-accordion-item>
                    </yd-accordion>
                </yd-infinitescroll>
            </div>
            <div class="pop-but" @click="popButClick()"></div>
        </div>
    </div>
</template>
<script>
import { Accordion, AccordionItem } from "vue-ydui/dist/lib.rem/accordion";
import { InfiniteScroll } from "vue-ydui/dist/lib.rem/infinitescroll";
import { Icons } from "vue-ydui/dist/lib.rem/icons";
import { Popup } from "vue-ydui/dist/lib.rem/popup";
import { Switch } from "vue-ydui/dist/lib.rem/switch";

export default {
  name: "monitor",
  data() {
    return {
      list: [],
      popShow: false,
      voltage: true,
      temperature: true,
      electricCurrent: true,
      electricLeakage: true,
      message: {
        action: "device",
        pagesize: 10,
        pageidx: 1
      },
      listShow: false
    };
  },
  created() {
    this.initLoad();
  },
  methods: {
    pullRefresh() {
      //'/static/mock/monitorPage.json'
      //'/weixin/wxhandler.ashx'
      this.message.pageidx++;
      this.$http
        .post(this.api.monitor, this.message, {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"
          },
          emulateJSON: true
        })
        .then(
          response => {
            var res = response.body;
            if (res.error === 0) {
              var dataLen = res.data.length;
              this.list = this.list.concat(res.data);
              this.listShow = true;
              if (dataLen.length === 0 || dataLen < this.message.pagesize) {
                this.$refs.infinitescroll.$emit(
                  "ydui.infinitescroll.loadedDone"
                );
              } else {
                this.$refs.infinitescroll.$emit(
                  "ydui.infinitescroll.finishLoad"
                );
              }
            } else {
              this.$dialog.alert({
                mes: "请求失败"
              });
            }
          },
          response => {
            this.$dialog.alert({
              mes: "Error:" + response.status + " " + response.statusText
            });
          }
        );
    },
    //初始化加载
    initLoad() {
      this.$dialog.loading.open();
      //   /static/mock/monitorPage.json
      //'/weixin/wxhandler.ashx'
      this.$http
        .post(this.api.monitor, this.message, {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"
          },
          emulateJSON: true
        })
        .then(
          response => {
            var res = response.body;
            if (res.error === 0) {
              var dataLen = res.data.length;
              this.list = res.data;
              if (dataLen.length === 0 || dataLen < this.message.pagesize) {
                this.$refs.infinitescroll.$emit(
                  "ydui.infinitescroll.loadedDone"
                );
              } else {
                this.$refs.infinitescroll.$emit(
                  "ydui.infinitescroll.finishLoad"
                );
              }
            } else {
              this.$dialog.toast({
                mes: res.data,
                timeout: 2500,
                icon: "error"
              });
            }
            this.$dialog.loading.close();
          },
          response => {
            this.$dialog.alert({
              mes: "Error:" + response.status + " " + response.statusText
            });
            this.$dialog.loading.close();
          }
        );
    },
    refresh() {
      // this.message.pageidx = 1;
      // this.$dialog.loading.open();
      // this.load(true);
      this.message.pageidx = 1;
      this.resultList = [];
      this.$refs.infinitescroll.$emit("ydui.infinitescroll.reInit");
      this.initLoad();
    },
    toChartPage(id, type) {
       if(id===undefined){
           this.$dialog.alert({
              mes: "参数异常"
            });
           return;
       }
      // this.$router.push({ path: "/chart", query: { type: type, id: id } });
      //传递params时，不要用path
      this.$router.push({ name: "chart", params: { type: type, id: id } });
    },
    popButClick() {
      this.popShow = true;
    }
  },
  components: {
    Accordion,
    AccordionItem,
    InfiniteScroll,
    Icons,
    Popup
  }
};
</script>
<style scoped>
.monitor {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}
/*overflow 这些样式不要直接加到 flex布局上*/
.container {
  flex-grow: 1;
  position: relative;
}

.pop-but {
  width: 50px;
  height: 50px;
  position: absolute;
  left: -25px;
  top: 50%;
  margin-top: -25px;
  background-color: #ccc;
  border-radius: 50%;
  border: 2px solid #aaa;
  opacity: 0.9;
  /* zoom: 999; */
}
.switch-list {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #f5f5f5;
  padding: 0 10px;
}
.switch-item {
  padding: 0.24rem;
  display: flex;
  flex-direction: row;
  background-color: #fff;
  margin-top: 0.35rem;
}
.switch-item span {
  height: 36px;
  line-height: 36px;
  margin-left: 10px;
}
.list-box {
  width: 100%;
  height: 100%;
  position: absolute;
  overflow-y: auto;
}

.list-item {
  /* text-align: left; */
  padding: 0.24rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-bottom: 1px solid #efe;
}

.item-content .unit {
  display: inline-block;
  width: 14px;
}
</style>


